import React, { useState, useEffect } from "react";

import { isArray } from "util";
import Filters from "../data/filters.json";

import {
  base64Encode,
  base64Decode,
  getParameterByName,
} from "../utils/helper";

const SECURE_SECRET = "fuck off";

export default function Project(props: any) {
  const { index, details, showPopup } = props;
  const {
    title,
    platform,
    filter,
    images,
    coverFocus = "top",
    live = true,
    secure = false,
  } = details;

  const _coverImage = images[0];
  const _filterClasses = isArray(filter) ? filter.join(" ") : filter;

  const _openModal = () => showPopup(index);

  const _allowProject = () => {
    let _secretRequest = getParameterByName("secret");
    if (_secretRequest) {
      if (base64Decode(_secretRequest) == SECURE_SECRET) return true;
    }

    return false;
  };

  const _directFilterStyle = () => {
    let _directFilterRequest = getParameterByName("tech");

    return _directFilterRequest
      ? {
          display: isArray(filter)
            ? filter.includes(_directFilterRequest)
              ? "block"
              : "none"
            : _directFilterRequest == filter
            ? "block"
            : "none",
        }
      : {};
  };
  // console.log("secure", secure, "_allowProject()", !_allowProject());

  // If project is set to "secure" but provided key is not valid
  if ((secure && !_allowProject()) || !live) return <div />;

  return (
    <div className={`item ${_filterClasses} col-sm-12 col-md-12 col-lg-6 mb-6`}>
      <a
        href="javascript:void(0);"
        className="item-wrap img-fluid"
        onClick={_openModal}
        style={{
          backgroundImage: `url(${_coverImage})`,
          backgroundPositionX: coverFocus,
        }}
      >
        <div className="work-info">
          <h3>{title}</h3>
          <span>{platform}</span>
        </div>
      </a>
    </div>
  );
}
