import React, { useEffect } from "react";

import Home from "./containers/Home";

function App() {
  useEffect(() => {
    window.addEventListener("load", _onDOMLoad);
  });

  const _onDOMLoad = () => _hideLoader();

  const _hideLoader = () => {
    const el = document.querySelector(".loading-container");
    if (el) el.remove();
  };

  return <Home />;
}

export default App;
